.text {
  /* display: inline-block;
  padding: 10px;
  margin: 10px;
  border: 1px solid black; */
  transition: transform 0.3s ease-in-out;
  font-family: 'SuisseIntl', sans-serif !important;
  transition: 0.3s ease all;
}

.text:hover {
  transform: scale(1.1); /* Zoom in by 10% */
  color: #d0ad11;
  font-family: 'SuisseIntl', sans-serif !important;
  letter-spacing: 0; /* Set letter-spacing to 0 on hover */
}



.FONTSS{
  font-family: 'SuisseIntl', sans-serif !important;
}

.card * {
  transition: 0.3s ease all;
}

.text .focus-content {
  display: block;
  padding: 8px 12px;
}

.text p {
  margin: 0;
  line-height: 1.5;
}

.text:hover .PARAA1, .card:focus-within img {
  margin-top: -80px;
}

.text:hover h3, .card:focus-within h3 {
  padding: 8px 12px 0;
}